import CardDota from "@/components/utils/cards/Card";
import CardBanner from "../../utils/cards/CardBanner";
import ButtonDota from "@/components/utils/cards/ButtonDota";
import Links from "./components/Links";
import { Progress } from "@nextui-org/react";
import RankDashboard from "./components/RanksDashboard";
import NextRank from "./components/NextRank";
import Map from "./components/MapDashboard";
import InvestmentCard from "./components/InvestmentCard";
import BonusCard from "./components/BonusCard";
import Upgrade from "./components/Upgrade";

const Dashboard = () => {
  return (
    <div className="flex justify-center w-full">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <CardBanner img="/img/buy-membership.png" position="bottom">
          <span className="text-3xl font-bold">Tienes tu membresía</span>
          <span className="text-xl font-black ">
            ¡Ya tienes tu membresía puedes ir a tu academia para ver los cursos!
          </span>
        </CardBanner>
        <CardBanner img="/img/buy-nft.png" position="top">
          <span className="text-3xl font-bold">Tienes un NFT</span>
          <ButtonDota rounded="full">
            <span className="text-[14px] font-bold">VER MIS NFTS</span>
          </ButtonDota>
        </CardBanner>
        <CardDota classNames="col-span-full">
          <div className="flex justify-between items-center">
            <span className="text-xl font-bold">Únete a nuestro canal</span>
            <ButtonDota rounded="md">
              <span className="text-[14px] font-bold">
                ÚNETE A NUESTRO CANAL
              </span>
            </ButtonDota>
          </div>
        </CardDota>
        <div className="grid grid-cols-2 gap-4 col-span-full items-center">
          <Upgrade />

          <Links />
        </div>

        <InvestmentCard />

        <BonusCard />

        {/* Este sera el marcador donde esta dota master */}
        <RankDashboard />

        {/* Este sera el marcador de la carrera */}
        <NextRank />

        <CardDota classNames="col-span-full h-[400px]">
          <Map />
        </CardDota>
      </div>
    </div>
  );
};

export default Dashboard;
