import CardDota from "@/components/utils/cards/Card";
import { formatNumberWithCommas } from "@/components/utils/formats/formatNumbers";
import useAxios from "@/hooks/useAxios";
import { Progress } from "@nextui-org/react";

const BonusCard = () => {
  const { data } = useAxios<{ limit: number; current: number }>({
    url: "users/current-multiplier",
    method: "get",
    defaultValue: "0",
  });
  const percent = data?.current ? (data.current / data?.limit) * 100 : 0;
  return (
    <CardDota classNames="flex flex-col gap-2 items-center">
      <span className="text-3xl font-bold text-dota">Multiplicador</span>
      <span className="text-[12px] ">hasta 3x</span>
      <div className="flex items-center gap-1">
        <span className="text-[12px]">$</span>{" "}
        <span className="text-[28px] font-bold">
          {formatNumberWithCommas(data?.limit, 0)}
        </span>
        <span className="text-[12px]">USD</span>
      </div>
      <Progress value={percent} />
      <span className="text-lg">
        $ {formatNumberWithCommas(data?.current, 0)} USD / ${" "}
        {formatNumberWithCommas(data?.limit, 0)} USD
      </span>
    </CardDota>
  );
};

export default BonusCard;
